import { Injectable } from "@angular/core";
import { Response } from "@angular/http";
import { Observable } from "rxjs/Observable";
import "rxjs/add/operator/catch";
import "rxjs/add/operator/map";
import { SettingsService } from "../../core/settings/settings.service";
import { AuthHttpService } from "../../auth/auth-http.service";
import { Facturation } from "../metier/facturation.metier";

@Injectable()
export class FacturationService {

  constructor(private settings: SettingsService,
    private authHttp: AuthHttpService) {
  }

  saveFacturation(facturation: Facturation): Observable<Response> {
    return this.authHttp.post(this.settings.baseUrl + `/facturation/save`, JSON.stringify(facturation));
  }

  findFacturationListByCriteria(facturation: Facturation): Observable<Array<Facturation>> {
    return this.authHttp.post(this.settings.baseUrl + `/facturation/search`, JSON.stringify(facturation))
      .map((res: Response) => res.json());
  }

  getFacturationList(): Observable<Facturation[]> {
    return this.authHttp.get(this.settings.baseUrl + '/facturation/list')
      .map((res: Response) => res.json());
  }

  getFacturation(id: number): Observable<Facturation> {
    return this.authHttp.get(this.settings.baseUrl + `/facturation/${id}`)
      .map((res: Response) => res.json());
  }

  annulerFacturation(facturation: Facturation): Observable<Response> {
    return this.authHttp.post(this.settings.baseUrl + `/facturation/annuler`, JSON.stringify(facturation));
  }

  getEtatPaiementList(): Observable<string[]> {
    return this.authHttp.get(this.settings.baseUrl + '/facturation/etatPaiementList')
      .map((res: Response) => res.json());
  }

  getTypePaiementList(): Observable<string[]> {
    return this.authHttp.get(this.settings.baseUrl + '/facturation/typePaiementList')
      .map((res: Response) => res.json());
  }

  getTypeFacturationServiceList(): Observable<string[]> {
    return this.authHttp.get(this.settings.baseUrl + '/facturation/typeFacturationServiceList')
      .map((res: Response) => res.json());
  }

}



