import {Injectable} from "@angular/core";
import {Response} from "@angular/http";
import {Observable} from "rxjs/Observable";
import "rxjs/add/operator/catch";
import "rxjs/add/operator/map";
import {Materiel} from "../metier/materiel.metier";
import {SettingsService} from "../../../core/settings/settings.service";
import {AuthHttpService} from "../../../auth/auth-http.service";

@Injectable()
export class MaterielService {

  constructor(private settings: SettingsService,
              private authHttp: AuthHttpService) {
  }

  saveMateriel(materiel: Materiel): Observable<Response> {
    return this.authHttp.post(this.settings.baseUrl + `/materiel/save`, JSON.stringify(materiel));
  }

  getMaterielList(): Observable<Materiel[]> {
    return this.authHttp.get(this.settings.baseUrl + '/materiel/list')
      .map((res: Response) => res.json());
  }

  getMateriel(id: number): Observable<Materiel> {
    return this.authHttp.get(this.settings.baseUrl + `/materiel/${id}`)
      .map((res: Response) => res.json());
  }

  deleteMateriel(materiel: Materiel): Observable<Response> {
    return this.authHttp.post(this.settings.baseUrl + `/materiel/delete`, JSON.stringify(materiel));
  }

  findMaterielListByCriteria(materiel: Materiel): Observable<Array<Materiel>> {
    return this.authHttp.post(this.settings.baseUrl + `/materiel/search`, JSON.stringify(materiel))
      .map((res: Response) => res.json());
  }
}



