import {Banque} from "../../admin/banque/metier/banque.metier";
import {Facturation} from "../../facturation/metier/facturation.metier";

export class Reglement {
  id: number;
  numero: number = 0;
  date: Date = null;
  typePaiement: string = null;
  facturation: Facturation = null;
  montant: number = 0;
  banque: Banque = null;
  numeroCheque: string = '';
  client?: string;

  constructor(values: Object = {}) {
    Object.assign(this, values);
  }
}



