import {Injectable} from "@angular/core";
import {Response} from "@angular/http";
import {Observable} from "rxjs/Observable";
import "rxjs/add/operator/catch";
import "rxjs/add/operator/map";
import {SettingsService} from "../../core/settings/settings.service";
import {AuthHttpService} from "../../auth/auth-http.service";
import {Intervention} from "../metier/intervention.metier";

@Injectable()
export class InterventionService {

  constructor(private settings:SettingsService,
              private authHttp:AuthHttpService) {
  }

  saveIntervention(intervention:Intervention):Observable<Response> {
    return this.authHttp.post(this.settings.baseUrl + `/intervention/save`, JSON.stringify(intervention));
  }

  findInterventionListByCriteria(intervention:Intervention):Observable<Array<Intervention>> {
    return this.authHttp.post(this.settings.baseUrl + `/intervention/search`, JSON.stringify(intervention))
      .map((res:Response) => res.json());
  }

  getInterventionList():Observable<Intervention[]> {
    return this.authHttp.get(this.settings.baseUrl + '/intervention/list')
      .map((res:Response) => res.json());
  }

  getIntervention(id:number):Observable<Intervention> {
    return this.authHttp.get(this.settings.baseUrl + `/intervention/${id}`)
      .map((res:Response) => res.json());
  }

  deleteIntervention(intervention:Intervention):Observable<Response> {
    return this.authHttp.post(this.settings.baseUrl + `/intervention/delete`, JSON.stringify(intervention));
  }

  getTypeInterventionList():Observable<string[]> {
    return this.authHttp.get(this.settings.baseUrl + '/intervention/typeInterventionList')
      .map((res:Response) => res.json());
  }

  getEtatInterventionList():Observable<string[]> {
    return this.authHttp.get(this.settings.baseUrl + '/intervention/etatInterventionList')
      .map((res:Response) => res.json());
  }

  getlastInterventionList():Observable<Intervention[]> {
    return this.authHttp.get(this.settings.baseUrl + '/intervention/lastInterventionList')
      .map((res:Response) => res.json());
  }

}



