import {Injectable} from "@angular/core";
import {Response} from "@angular/http";
import {Observable} from "rxjs/Observable";
import "rxjs/add/operator/catch";
import "rxjs/add/operator/map";
import {SettingsService} from "../../core/settings/settings.service";
import {AuthHttpService} from "../../auth/auth-http.service";
import {Installation} from "../metier/installation.metier";
import {Client} from "../../client/metier/client.metier";

@Injectable()
export class InstallationService {

  constructor(private settings: SettingsService,
              private authHttp: AuthHttpService) {
  }

  saveInstallation(installation: Installation): Observable<Response> {
    return this.authHttp.post(this.settings.baseUrl + `/installation/save`, JSON.stringify(installation));
  }

  getInstallationList(): Observable<Installation[]> {
    return this.authHttp.get(this.settings.baseUrl + '/installation/list')
      .map((res: Response) => res.json());
  }

  getInstallation(id: number): Observable<Installation> {
    return this.authHttp.get(this.settings.baseUrl + `/installation/${id}`)
      .map((res: Response) => res.json());
  }

  deleteInstallation(installation: Installation): Observable<Response> {
    return this.authHttp.post(this.settings.baseUrl + `/installation/delete`, JSON.stringify(installation));
  }

  findInstallationListByCriteria(installation: Installation): Observable<Array<Installation>> {
    return this.authHttp.post(this.settings.baseUrl + `/installation/search`, JSON.stringify(installation))
      .map((res: Response) => res.json());
  }

  getClientList(): Observable<Client[]> {
    return this.authHttp.get(this.settings.baseUrl + '/installation/clientList')
      .map((res: Response) => res.json());
  }
}



