import {Injectable} from "@angular/core";
import {Response} from "@angular/http";
import {Observable} from "rxjs/Observable";
import "rxjs/add/operator/catch";
import "rxjs/add/operator/map";
import {SettingsService} from "../../core/settings/settings.service";
import {AuthHttpService} from "../../auth/auth-http.service";
import {Reglement} from "../metier/reglement.metier";
import {Facturation} from "../../facturation/metier/facturation.metier";

@Injectable()
export class ReglementService {

  constructor(private settings: SettingsService,
              private authHttp: AuthHttpService) {
  }

  saveReglement(reglement: Reglement): Observable<Response> {
    return this.authHttp.post(this.settings.baseUrl + `/reglement/save`, JSON.stringify(reglement));
  }

  getReglementByFacturation(facturation: Facturation): Observable<Array<Reglement>> {
    return this.authHttp.post(this.settings.baseUrl + `/reglement/getReglementByFacturation`, JSON.stringify(facturation))
      .map((res: Response) => res.json());
  }

  getReglement(id: number): Observable<Reglement> {
    return this.authHttp.get(this.settings.baseUrl + `/reglement/${id}`)
      .map((res: Response) => res.json());
  }

  annulerReglement(reglement: Reglement): Observable<Response> {
    return this.authHttp.post(this.settings.baseUrl + `/reglement/annuler`, JSON.stringify(reglement));
  }

  getEtatPaiementList(): Observable<string[]> {
    return this.authHttp.get(this.settings.baseUrl + '/reglement/etatPaiementList')
      .map((res: Response) => res.json());
  }

  getTypePaiementList(): Observable<string[]> {
    return this.authHttp.get(this.settings.baseUrl + '/reglement/typePaiementList')
      .map((res: Response) => res.json());
  }

}



