import {Injectable} from "@angular/core";
import {Response, ResponseContentType} from "@angular/http";
import {Observable} from "rxjs/Observable";
import "rxjs/add/operator/catch";
import "rxjs/add/operator/map";
import {Profil} from "../metier/profil.metier";
import {SettingsService} from "../../../core/settings/settings.service";
import {AuthHttpService} from "../../../auth/auth-http.service";

@Injectable()
export class ProfilService {

  constructor(private settings: SettingsService,
              private authHttp: AuthHttpService) {
  }

  saveProfil(profil: Profil): Observable<Response> {
    return this.authHttp.post(this.settings.baseUrl + `/profil/save`, JSON.stringify(profil));
  }

  getProfilList(): Observable<Profil[]> {
    return this.authHttp.get(this.settings.baseUrl + '/profil/list')
      .map((res: Response) => res.json());
  }

  getProfil(id: number): Observable<Profil> {
    return this.authHttp.get(this.settings.baseUrl + `/profil/${id}`)
      .map((res: Response) => res.json());
  }

  deleteProfil(profil: Profil): Observable<Response> {
    return this.authHttp.post(this.settings.baseUrl + `/profil/delete`, JSON.stringify(profil));
  }

}



