import { Injectable } from "@angular/core";
import { Response } from "@angular/http";
import { Observable } from "rxjs/Observable";
import "rxjs/add/operator/catch";
import "rxjs/add/operator/map";
import { SettingsService } from "../../core/settings/settings.service";
import { AuthHttpService } from "../../auth/auth-http.service";
import { Reparation } from "../metier/reparation.metier";
import { Client } from "../../client/metier/client.metier";

@Injectable()
export class ReparationService {

  constructor(private settings: SettingsService,
    private authHttp: AuthHttpService) {
  }

  saveReparation(reparation: Reparation): Observable<Response> {
    return this.authHttp.post(this.settings.baseUrl + `/reparation/save`, JSON.stringify(reparation));
  }

  getReparationList(): Observable<Reparation[]> {
    return this.authHttp.get(this.settings.baseUrl + '/reparation/list')
      .map((res: Response) => res.json());
  }

  getReparation(id: number): Observable<Reparation> {
    return this.authHttp.get(this.settings.baseUrl + `/reparation/${id}`)
      .map((res: Response) => res.json());
  }

  deleteReparation(reparation: Reparation): Observable<Response> {
    return this.authHttp.post(this.settings.baseUrl + `/reparation/delete`, JSON.stringify(reparation));
  }

  findReparationListByCriteria(reparation: Reparation): Observable<Array<Reparation>> {
    return this.authHttp.post(this.settings.baseUrl + `/reparation/search`, JSON.stringify(reparation))
      .map((res: Response) => res.json());
  }

  getTypeReparationList(): Observable<string[]> {
    return this.authHttp.get(this.settings.baseUrl + '/reparation/typeReparationList')
      .map((res: Response) => res.json());
  }
}



