import {Injectable} from "@angular/core";
import {Response, ResponseContentType} from "@angular/http";
import {Observable} from "rxjs/Observable";
import "rxjs/add/operator/catch";
import "rxjs/add/operator/map";
import {Service} from "../metier/service.metier";
import {SettingsService} from "../../../core/settings/settings.service";
import {AuthHttpService} from "../../../auth/auth-http.service";

@Injectable()
export class ServiceService {

  constructor(private settings: SettingsService,
              private authHttp: AuthHttpService) {
  }

  saveService(service: Service): Observable<Response> {
    return this.authHttp.post(this.settings.baseUrl + `/service/save`, JSON.stringify(service));
  }

  getServiceList(): Observable<Service[]> {
    return this.authHttp.get(this.settings.baseUrl + '/service/list')
      .map((res: Response) => res.json());
  }

  getService(id: number): Observable<Service> {
    return this.authHttp.get(this.settings.baseUrl + `/service/${id}`)
      .map((res: Response) => res.json());
  }

  deleteService(service: Service): Observable<Response> {
    return this.authHttp.post(this.settings.baseUrl + `/service/delete`, JSON.stringify(service));
  }

  findServiceListByCriteria(service: Service): Observable<Array<Service>> {
    return this.authHttp.post(this.settings.baseUrl + `/service/search`, JSON.stringify(service))
      .map((res: Response) => res.json());
  }

  downloadPdf(): any {
    return this.authHttp.get(this.settings.baseUrl + '/service/getFile', {responseType: ResponseContentType.Blob}).map(
      (res) => {
        return new Blob([res.blob()], {type: 'application/pdf'})
      })
  }


}



