import {Injectable} from "@angular/core";
import {Response, ResponseContentType} from "@angular/http";
import {Observable} from "rxjs/Observable";
import "rxjs/add/operator/catch";
import "rxjs/add/operator/map";
import {Ville} from "../metier/ville.metier";
import {SettingsService} from "../../../core/settings/settings.service";
import {AuthHttpService} from "../../../auth/auth-http.service";

@Injectable()
export class VilleService {

  constructor(private settings: SettingsService,
              private authHttp: AuthHttpService) {
  }

  saveVille(ville: Ville): Observable<Response> {
    return this.authHttp.post(this.settings.baseUrl + `/ville/save`, JSON.stringify(ville));
  }

  getVilleList(): Observable<Ville[]> {
    return this.authHttp.get(this.settings.baseUrl + '/ville/list')
      .map((res: Response) => res.json());
  }

  getVille(id: number): Observable<Ville> {
    return this.authHttp.get(this.settings.baseUrl + `/ville/${id}`)
      .map((res: Response) => res.json());
  }

  deleteVille(ville: Ville): Observable<Response> {
    return this.authHttp.post(this.settings.baseUrl + `/ville/delete`, JSON.stringify(ville));
  }

  findVilleListByCriteria(ville: Ville): Observable<Array<Ville>> {
    return this.authHttp.post(this.settings.baseUrl + `/ville/search`, JSON.stringify(ville))
      .map((res: Response) => res.json());
  }

  downloadPdf(): any {
    return this.authHttp.get(this.settings.baseUrl + '/ville/getFile', {responseType: ResponseContentType.Blob}).map(
      (res) => {
        return new Blob([res.blob()], {type: 'application/pdf'})
      })
  }


}



