import {Injectable} from "@angular/core";
import {Response} from "@angular/http";
import {Observable} from "rxjs/Observable";
import "rxjs/add/operator/catch";
import "rxjs/add/operator/map";
import {Utilisateur} from "../metier/utilisateur.metier";
import {SettingsService} from "../../../core/settings/settings.service";
import {AuthHttpService} from "../../../auth/auth-http.service";

@Injectable()
export class UtilisateurService {

  constructor(private settings: SettingsService,
              private authHttp: AuthHttpService) {
  }

  saveUtilisateur(utilisateur: Utilisateur): Observable<Response> {
    return this.authHttp.post(this.settings.baseUrl + `/user/save`, JSON.stringify(utilisateur));
  }

  getUtilisateurList(): Observable<Utilisateur[]> {
    return this.authHttp.get(this.settings.baseUrl + '/user/list')
      .map((res: Response) => res.json());
  }

  getUtilisateur(id: number): Observable<Utilisateur> {
    return this.authHttp.get(this.settings.baseUrl + `/user/${id}`)
      .map((res: Response) => res.json());
  }

  deleteUtilisateur(utilisateur: Utilisateur): Observable<Response> {
    return this.authHttp.post(this.settings.baseUrl + `/user/delete`, JSON.stringify(utilisateur));
  }

}



