import { Injectable } from "@angular/core";
import { Response, Request, ResponseContentType } from "@angular/http";
import { Observable } from "rxjs/Observable";
import "rxjs/add/operator/catch";
import "rxjs/add/operator/map";
import { SettingsService } from "../../core/settings/settings.service";
import { AuthHttpService } from "../../auth/auth-http.service";
import { Client } from "../metier/client.metier";

@Injectable()
export class ClientService {

  constructor(private settings: SettingsService,
    private authHttp: AuthHttpService) {
  }

  saveClient(client: Client): Observable<Response> {
    return this.authHttp.post(this.settings.baseUrl + `/client/save`, JSON.stringify(client));
  }

  getClientList(): Observable<Client[]> {
    return this.authHttp.get(this.settings.baseUrl + '/client/list')
      .map((res: Response) => res.json());
  }

  getOnlyClientList(): Observable<Client[]> {
    return this.authHttp.get(this.settings.baseUrl + '/client/listClient')
      .map((res: Response) => res.json());
  }

  getClient(id: number): Observable<Client> {
    return this.authHttp.get(this.settings.baseUrl + `/client/${id}`)
      .map((res: Response) => res.json());
  }

  deleteClient(client: Client): Observable<Response> {
    return this.authHttp.post(this.settings.baseUrl + `/client/delete`, JSON.stringify(client));
  }

  getTypeClientList(): Observable<string[]> {
    return this.authHttp.get(this.settings.baseUrl + '/client/typeClientList')
      .map((res: Response) => res.json());
  }

  getEtatClientList(): Observable<string[]> {
    return this.authHttp.get(this.settings.baseUrl + '/client/etatClientList')
      .map((res: Response) => res.json());
  }

  findClientListByCriteria(client: Client): Observable<Array<Client>> {
    return this.authHttp.post(this.settings.baseUrl + `/client/search`, JSON.stringify(client))
      .map((res: Response) => res.json());
  }

  /*    Save and get the photo into vito-back    */
  /*
  savePhoto(file: File): Observable<Response> {
    return this.authHttp.request(this.settings.baseUrl + `/client/savePhoto`, file);
  }

  getClientPhoto(): Observable<string> {
    return this.authHttp.get(this.settings.baseUrl + '/client/getImage')
      .map((res: Response) => res.text());
  }
*/
}


