import {Injectable} from "@angular/core";
import {Response} from "@angular/http";
import {Observable} from "rxjs/Observable";
import "rxjs/add/operator/catch";
import "rxjs/add/operator/map";
import {SettingsService} from "../../core/settings/settings.service";
import {AuthHttpService} from "../../auth/auth-http.service";
import {Maintenance} from "../metier/maintenance.metier";

@Injectable()
export class MaintenanceService {

  constructor(private settings:SettingsService,
              private authHttp:AuthHttpService) {
  }

  saveMaintenance(maintenance:Maintenance):Observable<Response> {
    return this.authHttp.post(this.settings.baseUrl + `/maintenance/save`, JSON.stringify(maintenance));
  }

  findMaintenanceListByCriteria(maintenance:Maintenance):Observable<Array<Maintenance>> {
    return this.authHttp.post(this.settings.baseUrl + `/maintenance/search`, JSON.stringify(maintenance))
      .map((res:Response) => res.json());
  }

  getMaintenanceList():Observable<Maintenance[]> {
    return this.authHttp.get(this.settings.baseUrl + '/maintenance/list')
      .map((res:Response) => res.json());
  }

  getMaintenance(id:number):Observable<Maintenance> {
    return this.authHttp.get(this.settings.baseUrl + `/maintenance/${id}`)
      .map((res:Response) => res.json());
  }

  deleteMaintenance(maintenance:Maintenance):Observable<Response> {
    return this.authHttp.post(this.settings.baseUrl + `/maintenance/delete`, JSON.stringify(maintenance));
  }

  getTypeContratList():Observable<string[]> {
    return this.authHttp.get(this.settings.baseUrl + '/maintenance/typeContratList')
      .map((res:Response) => res.json());
  }

  getTypeFacturationList():Observable<string[]> {
    return this.authHttp.get(this.settings.baseUrl + '/maintenance/typeFacturationList')
      .map((res:Response) => res.json());
  }

}



