import {Injectable} from "@angular/core";
import {Response, ResponseContentType} from "@angular/http";
import {Observable} from "rxjs/Observable";
import "rxjs/add/operator/catch";
import "rxjs/add/operator/map";
import {Tiers} from "../metier/tiers.metier";
import {SettingsService} from "../../../core/settings/settings.service";
import {AuthHttpService} from "../../../auth/auth-http.service";

@Injectable()
export class TiersService {

  constructor(private settings: SettingsService,
              private authHttp: AuthHttpService) {
  }

  saveTiers(tiers: Tiers): Observable<Response> {
    return this.authHttp.post(this.settings.baseUrl + `/tiers/save`, JSON.stringify(tiers));
  }

  getTiersList(): Observable<Tiers[]> {
    return this.authHttp.get(this.settings.baseUrl + '/tiers/list')
      .map((res: Response) => res.json());
  }

  getTiers(id: number): Observable<Tiers> {
    return this.authHttp.get(this.settings.baseUrl + `/tiers/${id}`)
      .map((res: Response) => res.json());
  }

  deleteTiers(tiers: Tiers): Observable<Response> {
    return this.authHttp.post(this.settings.baseUrl + `/tiers/delete`, JSON.stringify(tiers));
  }

  findTiersListByCriteria(tiers: Tiers): Observable<Array<Tiers>> {
    return this.authHttp.post(this.settings.baseUrl + `/tiers/search`, JSON.stringify(tiers))
      .map((res: Response) => res.json());
  }

  downloadPdf(): any {
    return this.authHttp.get(this.settings.baseUrl + '/tiers/getFile', {responseType: ResponseContentType.Blob}).map(
      (res) => {
        return new Blob([res.blob()], {type: 'application/pdf'})
      })
  }


}



