import {Injectable} from "@angular/core";
import {Response} from "@angular/http";
import {Observable} from "rxjs/Observable";
import "rxjs/add/operator/catch";
import "rxjs/add/operator/map";
import {Banque} from "../metier/banque.metier";
import {SettingsService} from "../../../core/settings/settings.service";
import {AuthHttpService} from "../../../auth/auth-http.service";

@Injectable()
export class BanqueService {

  constructor(private settings: SettingsService,
              private authHttp: AuthHttpService) {
  }

  saveBanque(banque: Banque): Observable<Response> {
    return this.authHttp.post(this.settings.baseUrl + `/banque/save`, JSON.stringify(banque));
  }

  getBanqueList(): Observable<Banque[]> {
    return this.authHttp.get(this.settings.baseUrl + '/banque/list')
      .map((res: Response) => res.json());
  }

  getBanque(id: number): Observable<Banque> {
    return this.authHttp.get(this.settings.baseUrl + `/banque/${id}`)
      .map((res: Response) => res.json());
  }

  deleteBanque(banque: Banque): Observable<Response> {
    return this.authHttp.post(this.settings.baseUrl + `/banque/delete`, JSON.stringify(banque));
  }

  findBanqueListByCriteria(banque: Banque): Observable<Array<Banque>> {
    return this.authHttp.post(this.settings.baseUrl + `/banque/search`, JSON.stringify(banque))
      .map((res: Response) => res.json());
  }
}



